import React from 'react';





class CasesDisplay extends React.Component
{
    style ={
        row:
        {
            fontSize:'14px',
            border: '1px solid rgba(0, 0, 0, 0.05)',
            boxShadow: '0px 3px 5px rgba(0, 0, 0, 0.05)',
            borderRadius: '6px',
            lineHeight: '50px',
            padding: '10px 16px',
            display:'flex'
        },

        confirmed: {
            color: '#ff073a'
        },
        active :{
            color: '#007bff'
        },
        recovered: {
            color: '#28a745'
        },
        deaths :{
            color: '#6c757d'
        }

    }
    render()
    {
        const {data} = this.props;
        return(
            <React.Fragment >
               <div style={this.style.row}>
               <div className="col" style={{textAlign:'center',padding:'0px 20px 0px 20px'}}>
                    <p style={this.style.confirmed}>
                        Confirmed 
                    </p>
                    <p style={{lineHeight:'5px',fontSize:'10px',fontWeight:'100'}}>[+{(+data.deltaconfirmed).toLocaleString()}]</p>
        <h4>{(+data.confirmed).toLocaleString()}</h4>
                </div>
               <div className="col" style={{textAlign:'center',padding:'0px 20px 0px 20px'}}>
                    <p style={this.style.active}>
                        Active
                    </p>
                    <p style={{lineHeight:'5px',fontSize:'10px',fontWeight:'100'}}> -</p>
        <h4>{(+data.active).toLocaleString()}</h4>
                </div>
               <div className="col" style={{textAlign:'center',padding:'0px 20px 0px 20px'}}>
                    <p style={this.style.recovered}>
                        Recovered
                    </p>
                    <p style={{lineHeight:'5px',fontSize:'10px',fontWeight:'100'}}>[+{(+data.deltarecovered).toLocaleString()}]</p>
        <h4>{(+data.recovered).toLocaleString()}</h4>
                </div>
               <div className="col" style={{textAlign:'center',padding:'0px 20px 0px 20px'}}>
                    <p style={this.style.deaths}>
                        Deaths
                    </p>
                    <p style={{lineHeight:'5px',fontSize:'10px',fontWeight:'100'}}>[+{(+data.deltadeaths).toLocaleString()}]</p>
                    <h4>{(+data.deaths).toLocaleString()}</h4>
                </div>
               
                
               </div>
            </React.Fragment>
        );
    }
}


export default CasesDisplay;