import React from 'react';

import axios from 'axios';

import Navbar from './navbar';

import Home from './Home';
import {About} from './AboutInfo';

import {Route, Switch} from 'react-router-dom';
import StateInfo from './StateInfo';


class App extends React.Component
{
    state={
        india : [],
        statewise: [],
        tested:[],
        states_tested_data:[],
        districtwise: [],
        zones:[],
        statePopulationData: [],
        default:'India'

    }

    

    style ={
        area:{
            display: 'flex',
            background: '#FFF',
            border: '1px solid rgba(0, 0, 0, 0.05)',
            boxShadow: '0px 3px 5px rgba(0, 0, 0, 0.05)',
            borderRadius: '6px',
            fontSize: '12px',
            lineHeight: '20px',
            padding: '10px 16px',
            cursor: 'pointer',
            userSelect: 'none',
            position: 'relative',
            flex: '1',
            alignItems: 'center'
        },

        areaName :
        {
            fontWeight: '600',
            overFlow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            flex: '1'
        }
    }
    constructor()
    {
        super();
        this.getStateTestData = this.getStateTestData.bind(this);
    }


    async componentDidMount(){
        const {data}=await axios('https://api.covid19india.org/data.json');
        const {data:{zones}} =await  axios('https://api.covid19india.org/zones.json');
        const result = await axios('https://api.covid19india.org/v2/state_district_wise.json');
        
        
         const {data:{states_tested_data}}=await axios('https://api.covid19india.org/state_test_data.json');
            

        const {statewise} = data;
   
        const india = statewise.filter(s => s.statecode === 'TT')[0];
        const s = statewise.filter(s=>s.statecode !== 'TT');


        
        //this.setState({zones});
        const tested = data.tested[data.tested.length-1];
       // this.setState({india,tested});
        
        this.setState({statewise:s,districtwise:result.data,india,tested,zones,states_tested_data});

        
        //console.log(this.state.zones);

    }


    getStateTestData(code)
    {
        const d =
            this.state.statewise.filter(st => st.statecode=== code);
        const name = d[0] === undefined? '' : d[0].state;

        
        const result=this.state.states_tested_data.filter(d=> d.state===name);
          

         return result[result.length-2];
        
    }




    render()
    {
        return (
            
            <React.Fragment>
               <div>
                   
                <Navbar />
                
                <div className="content">
                <Switch>
                    <Route path="/about">
                        <About />
                    </Route>
                    <Route path="/state/:code?" component={(props)=>
                        <StateInfo zones={this.state.zones} getStateTestData={(code)=>this.getStateTestData(code)} statewise={this.state.statewise} districtwise={this.state.districtwise} {...props}/>} />
                    
                    <Route exact path="/">
                        <Home data={this.state}/>
                    </Route>
          
        </Switch>
                
               </div>
               </div>
               
                
            </React.Fragment>
        );
    }
}


export default App;

